      <i><b> [Citation Needed]</b> </i>
    </h1>

    <h2>A tool for quickly finding reputable citations</h2>

    <!-- input with submit button -->
    <input
      @keydown.enter="search"
      type="text"
      class="
        flex-1
        border-2 border-gray-300
        bg-white
        h-10
        p-5
        pr-16
        mb-4
        rounded-lg
        text-lg
        focus:outline-none
        disabled:opacity-50
      "
      v-model="searchQuery"
      placeholder="Search for a claim"
    />

    <div class="flex flex-row justify-between">
      <!-- radio with 2 options -->
      <div class="flex flex-row">
        <div class="flex flex-row items-center mr-4">
          <input
            class="mr-2"
            type="radio"
            id="narrow"
            name="search"
            value="narrow"
            v-model="searchType"
            checked
          />
          <label for="narrow">Narrow</label>
        </div>
        <div class="flex flex-row items-center">
          <input
            class="mr-2"
            type="radio"
            id="general"
            name="search"
            value="general"
            v-model="searchType"
          />
