<template>
  <div :class="genClass">
    <div class="pb-2 pt-2 px-4">
      <h2>
        <span>
          <img src="@/assets/search.svg" class="h-5 inline" />
          {{ query }}
        </span>
      </h2>

      <div v-if="state == 'success'">
        <!-- divider -->
        <div class="relative py-2">
          <div class="absolute inset-0 flex items-center">
            <div class="w-full border-b border-gray-300"></div>
          </div>
          <div class="relative flex justify-center">
            <span class="bg-white px-4 text-sm text-gray-500">Snippet</span>
          </div>
        </div>

        <span>
          {{ glance(resp.text) }}
          <br />
          <i>
            Read more at
            <a :href="'https://wikipedia.org/wiki/' + resp.page">{{
              resp.page.replaceAll("_", " ")
            }}</a>
          </i>
        </span>

        <div class="relative py-2">
          <div class="absolute inset-0 flex items-center">
            <div class="w-full border-b border-gray-300"></div>
          </div>
          <div class="relative flex justify-center">
            <span class="bg-white px-4 text-sm text-gray-500">Citations</span>
          </div>
        </div>

        <div class="list-decimal text-left px-4">
          <li v-for="citation in resp.citations" :key="citation">
            <span v-if="citation.charAt(0) == '<'" v-html="citation"></span>
            <span v-else>{{ citation }}</span>
            <br />
          </li>
        </div>
      </div>

      <div v-if="state == 'loading'">
        <img class="mx-auto" src="@/assets/search_animate.svg"/> 
      </div>

      <div v-if="state == 'error'">
        {{ resp }}
      </div>
    </div>
  </div>
</template>

<script>
const axios = require("axios");
const api = "";

export default {
  props: {
    query: String,
    type: {
      type: String,
      default: "narrow",
    },
  },

  data() {
    return {
      resp: {
        page: "",
        text: "",
        citations: [],
      },
      state: "loading",
      qtype: "narrow",
    };
  },

  computed: {
    genClass() {
      let base = "border-2 border-gray-500 rounded-md ";

      if (this.state == "loading") {
        return base + "border-gray-200";
      } else if (this.state == "error") {
        return base + "border-red-200";
      } else if (this.state == "success") {
        return base + "border-green-200";
      } else {
        return base + "border-gray-200";
      }
    },
  },

  methods: {
    glance(str) {
      // remove all [x] patterns
      str = str.replaceAll(/\[.*?\]/g, "");
      if (str.length > 170) {
        return (
          str.substring(0, 75) +
          " ... " +
          str.substring(str.length - 75, str.length)
        );
      } else {
        return str;
      }
    },
  },

  mounted() {
    this.qtype = this.type;
    console.log(this.query);
    console.log(this.qtype);

    axios
      .get(api + `/${this.qtype}/` + this.query)
      .then((response) => {
        console.log(response.data);
        this.state = "success";
        this.resp = response.data;
      })
      .catch((error) => {
        console.log(error);
        this.state = "error";
        this.resp = error;
      });
  },
};
</script>

<style lang="scss" scoped>
</style>